.wp-caption-text {
  margin: 0 !important;
  color: #999;
  display: block;
  text-align: right;
  font-size: 10px; }
.wp-caption {
  width: auto !important; }
.article {
  font-style: 14px;
  line-height: 1.57;
  figure {
    img {
      width: 100%; }

    figcaption {
      color: #999;
      display: block;
      text-align: right;
      font-size: 10px; } }
  p, ul, ol {
    margin: 1em 0;
    &:first-child {
      margin-top: 0; }
    &:last-child {
      margin-bottom: 0; } }
  img {
    max-width: 100%;
    height: auto !important;
    width: auto !important; }
  > img {
    display: block;
    margin: percentage(40/720) 0;
    &:first-child {
      margin-top: 0; }
    &:last-child {
      margin-bottom: 0; } }
  ul {
    > li {
      &:before {
        content: "- "; } } }
  ol {
    list-style: decimal;
    padding-left: 18px; }
  @include sm {
    > img {
      margin-left: -10px;
      margin-right: -10px;
      max-width: calc(100% + 20px); } } }
.article__source {
  color: #8a92a1;
  font-style: italic; }
.article__title {
  margin-bottom: 24px;
  @include f(r, 18);
  line-height: 1.667; }

.article-head {
  position: relative;
  margin-bottom: 70px;
  padding-right: percentage(120/960);
  font-size: 16px;
  line-height: 1.625;
  .title-thin {
    margin-bottom: 15px; }
  .share {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 0; }
  @include sm {
    padding-right: 0;
    margin-bottom: 50px;
    .share {
      position: relative;
      margin-top: 25px; }
    .share-btn {
      display: inline-block;
      margin: 0 12px 0 0; } } }

.article-aside {
  border-top: 4px solid $color-yellow;
  padding-top: 10px;
  .title {
    margin-bottom: 7px; } }
