.bla {
  p {} }
.l-main-page {
  @include clr;
  @include r($bp2) {
    .l-main-page__left,
    .l-main-page__right {
      float: none;
      width: auto;
      margin: 0;
      max-width: none; }
    .l-main-page__right {
      margin-top: 50px; } } }

.l-main-page__left {
  float: left;
  width: 100%;
  max-width: 310px;
  margin-right: -310px; }

.l-main-page__right {
  float: right;
  width: percentage(550/960);
  // margin-left: 360px
  max-width: 550px; }

.l-news-page {
  @include clr;
  @include sm {
    .l-news-page__left {
      float: none;
      width: auto;
      margin: 0;
      padding: 0; }
    .l-news-page__right {
      display: none; } } }

.l-news-page__left {
  float: left;
  width: 330px;
  margin-right: -330px;
  box-sizing: border-box;
  padding-right: 20px;
  position: relative;
  overflow: hidden;
  @include rmin($bp2) {
    max-height: 601px; } }

.l-news-page__right {
  float: right;
  margin-left: 350px;
  max-width: 590px; }

.l-article {
  margin-bottom: 75px;
  @include clr;
  @include sm {
    margin-bottom: 60px;
    .l-article__aside {
      float: none;
      width: auto;
      margin-bottom: 20px; }
    .l-article__content {
      float: none;
      width: auto; } } }

.l-article__aside {
  float: left;
  width: percentage(180/960);
  max-width: 180px; }

.l-article__content {
  float: right;
  width: percentage(720/960); }
