.contacts {
	padding-bottom: 30px;
	padding-top: 30px;
	@include clr;
	&__left {
		float: right;
		width: 280px;
		@include r(800) {
			float: none;
			width: auto; } }
	&__right {
		overflow: hidden;
		padding-right: 30px;
		@include r(800);
		float: none;
		width: auto;
		img {
			width: 100%; } } }
.vacancy-wrapper {
	padding-top: 40px; }
.vacancy {
	margin-bottom: 40px;
	.vacancy__title {
		@include f(r,16);
		margin-bottom: 10px;
		a {
			color: #19294A;
			text-decoration: none; } }

	.vacancy__download {
		text-decoration: none;
		color: #777;
		i {
			font-size: 28px;
			margin-right: 10px;
			display: inline-block;
			vertical-align: middle;
			color: #ffde00; } } }

.select {
	border: 1px solid #435679;
	position: relative;
	select {
		opacity: 0;
		appearance: none;
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		z-index: 1; }
	.select__value {
		height: 54px;
		line-height: 54px;
		padding-left: 20px;
		font-size: 14px;
		color: #1a2a49;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		padding-right: 24px; }
	.select__arrow {
		color: #000;
		font-size: 20px;
		position: absolute;
		right: 16px;
		top: 50%;
		margin-top: -9px;
		color: #435679; } }
.input, textarea {
	border: 1px solid #435679;
	height: 54px;
	line-height: 54px;
	padding-left: 20px;
	padding-right: 20px;
	@include f(r,14);
	width: 100%;
	box-sizing: border-box; }
textarea {
	height: 124px; }

.form {
	margin: 50px 0;
	h2 {
		font-size: 24px;
		@include f(b,24);
		margin-bottom: 50px;
		color: #24385e;
		@include r(750) {
			margin-bottom: 25px; } }
	.form__field_1,.form__field_2,.form__field_3 {
		float: left; }
	.form__field_1 {
		width: 36%;
		margin-right: 10%; }
	.form__field_2 {
		float: left;
		width: 22%;
		margin-right: 10%; }
	.form__field_3 {
		float: right;
		width: 22%; }
	.form__field_4 {
		clear: both; }
	&__button {
		text-align: right;
		@include r(550) {
			display: block;
			.btn {
				width: 100%; } } }
	.form__field {
		margin-bottom: 40px;
		@include r(750) {
			margin: 0;
			float: none;
			width: 100%;
			margin-bottom: 10px; }
		label {
			margin-bottom: 12px;
			font-style: 16px;
			display: block; } } }
.contact {
	margin-top: 50px;
	font-size: 16px;
	line-height: 24px;
	color: #1a2a49;
	p {
		margin-bottom: 14px; }
	&__schedule {
		font-size: 14px;
		span {
			color: #a1a1a1;
			text-transform: uppercase; } }
	ul {
		font-size: 14px;
		margin-top: 35px; }
	li {
		margin-bottom: 19px;
		padding-left: 40px;
		position: relative;
		line-height: 1.3;
		i {
			position: absolute;
			left: 0;
			top: -1px;
			color: #fedc32;
			font-size: 23px;
			&.icon-facebook {
				color: #fff;
				font-size: 13px;
				width: 21px;
				border-radius: 50%;
				line-height: 21px;
				height: 21px;
				background: #fedc32;
				text-align: center; } } } }
.q {
	display: block;
	text-decoration: none;
	padding: 14px 14px 20px 73px;
	position: relative;
	font-size: 14px;
	color: #1a2a49;
	line-height: 22px;
	min-height: 40px;
	&.is-active,&:hover {
		&::before {
			position: absolute;
			left: 0;
			top: 0;
			content: '';
			bottom: 0;
			width: 5px;
			background: #fedc32; } }
	.q__num {
		@include f(l,36);
		position: absolute;
		left: 14px;
		line-height: 1; } }
